import io from 'socket.io-client';
import { SOCKET_CLIENT_URL } from './api';

export const SOCKET_CONNECTION = io(`${SOCKET_CLIENT_URL}`, {
    transports: ["websocket"],
    transport: ["websocket"],
    upgrade: true
})

export const emitEvent = (event, data) => {
    SOCKET_CONNECTION.emit(event, data);
};

export const onEvent = (event, func) => {
    SOCKET_CONNECTION.on(event, func);
};

